import React from "react";
import OwlCarousel from "react-owl-carousel";
import Heading from "./Heading";

const TopNfts = [
  {
    id: "1",
    name: "Degenerate Santa #1",
    price: "ETH 9.31",
    image: "/images/santa2.jpg",
  },
  {
    id: "2",
    name: "Degenerate Santa #2",
    price: "ETH 1.31",
    image: "/images/santa3.jpg",
  },
  {
    id: "3",
    name: "Degenerate Santa #3",
    price: "ETH 2.01",
    image: "/images/example.jpg",
  },
  {
    id: "4",
    name: "Degenerate Santa #4",
    price: "ETH 0.41",
    image: "/images/santa5.jpg",
  },
  {
    id: "5",
    name: "Degenerate Santa #5",
    price: "ETH 9.31",
    image: "/images/santa4.jpg",
  },
  {
    id: "6",
    name: "Degenerate Santa #6",
    price: "ETH 12.31",
    image: "/images/santa6.jpg",
  },
];

function TopNFT() {
  return (
    <div className="my-12">
      <Heading title={"TOP SANTAS"} />
      <OwlCarousel
        className="owl-theme"
        loop
        autoplay={true}
        autoplaySpeed={2000}
        smartSpeed={1000}
        autoplayTimeout={3000}
        responsive={{
          0: {
            margin: 2,
            items: 1,
          },
          640: {
            items: 2,
            margin: 10,
          },
          1024: {
            items: 3,
            margin: 20,
          },
          1536: {
            items: 4,
            margin: 30,
          },
        }}
      >
        {TopNfts.map((t) => (
          <div
            key={t.id}
            className="top-hover relative border-8 border-subLightMain pb-5 bg-subMain flex-colo"
          >
            <div className="relative w-full">
              <img
                src={t.image}
                alt={t.name}
                className="w-full h-64 object-cover"
              />
              <div className="top-hidden absolute transitione top-1 bottom-1 left-1 right-1 bg-overlay flex-colo">
                <h2 className="font-semibold text-whites">{t.name}</h2>
               
              </div>
            </div>

            <button className="px-6 text-whites tracking-wide py-3 mt-5 bt-linear font-bold text-sm rounded">
               {t.price}
            </button>
          </div>
        ))}
      </OwlCarousel>
    </div>
  );
}

export default TopNFT;
